.travel-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: center;
    height: 600px;
    width: 880px;
    background: #f2f2f2;
    border-radius: 75px;
    padding-left: 5px;
    padding-top: 5px;
}

.buttons-container{
    display: flex;        
}

.tripInfo-container {
    margin-top: 10px;
}

.tripInfo-container p {
    margin: 0;
}

.button{
    /* width: 150px; */
    height: 40px;
    border-radius: 1em;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px 0px;
    border: 0.5px solid #333333;
    padding: 0px 30px;
    cursor: pointer;
    font-weight: bold;
}

.confirm{
    background-color: #38b338;
}

.decline{
    background-color: #ff0000;
}

